import { useState, useEffect } from 'react';
import About from './About';
import './App.css';
import CP from './CP';
import Education from './Education';
import Header from './Header';
import Projects from './Projects';
import Social from './Social'

function App() {
  
  const ColorPrimary =    ['#2B3A55', '#61764B', '#2B3A55', '#61764B', '#1A120B'];
  const ColorSecondary =  ['#0F6280', '#9BA17B', '#0F6280', '#9BA17B', '#3C2A21'];
  const ColorBg =         ['#85A8B5', '#CFB997', '#85A8B5', '#CFB997', '#D5CEA3'];
  const ColorAccent =     ['#F2E5E5', '#FAD6A5', '#F2E5E5', '#FAD6A5', '#E5E5CB'];
  
  let topPos = [0, 0, 0, 0];


  
  const scrollToY = (top) => {
    window.scrollTo({
      top: top,
      behavior: "smooth",
    });
  }

  const setTheme = (id) => {
    document.documentElement.style.setProperty('--Primary', ColorPrimary[id]);
    document.documentElement.style.setProperty('--Secondary', ColorSecondary[id]);
    document.documentElement.style.setProperty('--Bg', ColorBg[id]);
    document.documentElement.style.setProperty('--Accent', ColorAccent[id]);
    
    document.documentElement.style.setProperty('--SocialVis', id == 0? 'visible' : 'hidden');
  
  }

  const scrollToSection = (sectionId) => {

    setTheme(sectionId);
    // scrollToY((window.innerHeight*0.9*sectionId));
    scrollToY(topPos[sectionId]);
  }

  let isScrolling, prevY = 0;

  window.addEventListener('scroll', (event) => {
    window.clearTimeout( isScrolling );
	  isScrolling = setTimeout(function() {
      const height = window.innerHeight, pos = window.scrollY;
      
      for(let i = 0; i < 4; i++) if(Math.abs(pos - topPos[i]) < height / 10) scrollToSection(i);


      pos >= height * 0.45 * 6 ? setTheme(3) :
      pos >= height * 0.45 * 4 ? setTheme(2) :
      pos >= height * 0.45 * 2 ? setTheme(1) : setTheme(0)

      prevY = pos;

	  }, 66);
  })

  useEffect(() => {
    const h = window.innerHeight;
    for(let i = 0; i < 4; i++) topPos[i] = h * 0.9 * i;    
  }, [])
  

  return (
    <div className="App">
      <Header scrollToSection = {scrollToSection}/>
      <Social/>
      {/* <div className='bodyPadding'/> */}
      <div className='body'>
        <About/>
        <Education/>
        <CP/>
        <Projects/>
      </div>
    </div>
  );
}

export default App;
