import React, {useState, useRef, useEffect} from 'react'



export default function About() {

  const target = useRef(null);

  const [show, setShow] = useState(false);

  
  useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      console.log(entries);
      setShow(entries[0].isIntersecting)
    }, {
      root: null,
      rootMargin: '0px',
      threshold: 0.5
    });
  
    if(target.current) observer.observe(target.current)
  
    return () => {
      if(target.current) observer.unobserve(target.current)
    }
  }, [target])
  

  return (
    <div className='aboutContainer'>
      <div className='aboutTextContainer' ref = {target}>
        <text className={show?'showAbt' :'hideAbt'}> Hi, this is Jamil </text>
        <text className={show?'showAbt' :'hideAbt'}> Here is an interesting fact about me </text>
        <text className={show?'showAbt' :'hideAbt'}> Here is another interesting fact about me </text>
        <text className={show?'showAbt' :'hideAbt'}> Here is yet another interesting fact about me </text>

      </div>
    </div>
  )
}
