import { School } from '@mui/icons-material';
import React, { useState } from 'react'

export default function Education() {
  const Schools = [ [require('./resources/school_icons/jamia.png'), 'https://www.sjiscsylhet.edu.bd', 'Shahjalal Jamia Islamia School and College', '( Kinder Garden - Class 2 )'],
                    [require('./resources/school_icons/pilot.png'), 'https://www.sylgovpilothss.edu.bd', 'Sylhet Government Pilot High School', '( Class 3 - SSC )'],
                    [require('./resources/school_icons/mc.png'), 'https://mccollege.edu.bd', 'MC College, Sylhet', '( HSC )'],
                    [require('./resources/school_icons/sust.png'), 'https://www.sust.edu', 'Shahjalal University of Science and Technology', '( IICT || Software Engineering)']];

  const [activeSchool, setActiveSchool] = useState (3);
                    
  const SchoolSide = (id) => {
    return(
      <div className='school schoolSide' onClick={() => setActiveSchool(id)}>
        <img src={Schools[id][0]}/>
      </div>
    )
  }
  const SchoolSiide = (id) => {
    return(
      <div className='school schoolSiide' onClick={() => setActiveSchool(id)}>
        <img src={Schools[id][0]}/>
      </div>
    )
  }
  const SchoolCenter = (id) => {
    return(
      <div className='school schoolCenter'>
        <img src={Schools[id][0]}/>
        <div className='schoolDetails linkNormal'>
          <a href={Schools[id][1]}>{Schools[id][2]}</a>
          <p>{Schools[id][3]}</p>
        </div>
      </div>
    )
  }

  return (
    <>{

      window.innerWidth <1000 ?
      <div className='educationContainer section'>
        {SchoolSide(activeSchool)}
      </div>
      :

    <div className='educationContainer section'>
    {SchoolSiide( (activeSchool + 2) % 4 )}
    {SchoolSide( (activeSchool + 3) % 4 )}
    {SchoolCenter(activeSchool)}
    {SchoolSide( (activeSchool + 1) % 4)}
    {SchoolSiide( (activeSchool + 2) % 4)}
    </div>
  }
  </>
    )
}
