import React, {useState} from 'react'

export default function CP() {

  const Achievemetns = [[require('./resources/cp_icons/cf.png'), 'Codeforces Max Rating : 1900+', ''],
                        [require('./resources/cp_icons/chef.png'), 'Codechef Max Rating : 2050+', ''],
                        [require('./resources/cp_icons/solved.png'), 'Total Problems Solved  : 2000+', ''],
                        [require('./resources/cp_icons/top-10.png'), 'Top 10', 'ICPC Asia West Regional Preliminary : 2020'],
                        [require('./resources/cp_icons/top-10.png'), 'Top 10', 'AUST Inter University Programming Contest : 2022']];

  const Profiles = [[require('./resources/cp_icons/cfFull.png'),require('./resources/cp_icons/cf.png'), 'https://codeforces.com/jamil314', 'cf://jamil314'],
                    [require('./resources/cp_icons/chefFull.png'),require('./resources/cp_icons/chef.png'), 'https://codechef.com/jamil314', 'cc://jamil314']];


  return (
    <div className='cpContainer section'>
      <div className='cpProfiles flexColumn' >
        {
          Profiles.map((profile) => {
            return (
              <div className='cpProfile'>
                <div class="flipCard">
                  <div class="flipFront" style={{backgroundColor : 'white', borderRadius : '5vh'}}>
                    <img src = {profile[0]} style={{paddingRight : '2vw'}}/>
                  </div>
                  <div class="flipBack flexRow">
                    <img src = {profile[1]}/>
                    <div className='flexColumn'>
                      <a href={profile[2]} style = {{textDecoration : 'none' , color : 'var(--Accent)', textAlign : 'center'}}>{profile[3]}</a>
                    </div>
                  </div>
                </div>
              </div>
            )
          })
        }
      </div>

      <div className='cpAchievements'>
        {
          Achievemetns.map((achievement, id) => {
            return(
              <div className='cpAchievement' style={{'--delay' : /*id == 3? -2 :*/ id - 2}} >
                <img src={achievement[0]}/>
                <div className='cpAchText'>
                  <div> {achievement[1]} </div>
                  <div> {achievement[2]} </div>
                </div>
              </div>
            )
          })
        }
      </div>
    </div>
  )
}



