import React, {useRef, useState, useEffect} from 'react'
import Menu from './Menu';
import MenuOpenSharpIcon from '@mui/icons-material/MenuOpenSharp';
export default function Header(props) {

  const [height, setHeight] = useState(window.innerHeight/10);
  const [mobile, setMobile] = useState(false);
  const [show, setShow] = useState(false);

  const checkOrientation = () => {
    if(window.innerWidth < 720) {
      setMobile(true)
      setHeight(window.innerHeight/10);
      setShow(false)
    } else {
      setMobile(false)
      setHeight(window.innerHeight/10);
    }
  }

  const toggleShow = () => {
    if(show) {
      setShow(false);
      setHeight(window.innerHeight/10);
    } else {
      setShow(true);
      setHeight(window.innerHeight/2);
    }
  }

  window.addEventListener('resize', checkOrientation)

  useEffect(() => {
    checkOrientation();
  }, [])
  

  return (
    <div  style = {{height: height}} className='headerContainer' >
        {mobile?
        <div className='header' style={{flexDirection: 'column'}}>
              <div className='header' style={{paddingLeft : 0, width : window.innerWidth * 0.9}}>
                Md. Jamilur Rahman
                <MenuOpenSharpIcon onClick = {toggleShow} className = 'menuButton' />
              </div>
              {show?<Menu scrollToSection = {props.scrollToSection} mobile = {true}/>:null}
          </div>
          :
          <div className='header'>
              Md. Jamilur Rahman
              <Menu scrollToSection = {props.scrollToSection} mobile = {false}/>
          </div>}
    </div>
  )
}
